<script>
    import appCreateReviewModal from '../modal/CreateReviewModal'
    import appUpdateReviewModal from '../modal/UpdateReviewModal'
    export default {
        components: {
            appCreateReviewModal,
            appUpdateReviewModal,
        },
        props: [
            'auth',
            'currentOrder'
        ],
        methods: {
            // ອະນຸມັດງານ
            async orderApprove () {
                const willDelete = await this.$swal({
                    title: 'ຕ້ອງການທີ່ຈະອະນຸມັດງານ ຫຼື ບໍ່?',
                    type: 'warning',
                    showCancelButton: true,
                    confirmButtonColor: 'primary',
                    cancelButtonColor: '#000',
                    confirmButtonText: 'ອະນຸມັດ',
                    cancelButtonText: 'ກັບຄືນ'
                })
                if (willDelete.value) {
                    await this.$axios.put('order/order-approve', {_id: this.currentOrder._id})
                    this.$swal({
                        title: 'ສົ່ງມອບວຽກແລ້ວ',
                        type: 'success',
                        toast: true,
                        position: 'top',
                        showConfirmButton: false,
                        timer: 2000,
                    })
                    this.$io.emit('update-quotation')
                }
            }
        }
    }
</script>

