<script>
    import appOrderLoading from '../loading/message/OrdersLoading'
    export default {
        components: {
          appOrderLoading
        },
        props: [
            'orders',
            'auth',
            'currentOrder',
            'fetchOrders',
            'messageMobileFunc',
            'orderLoading'
        ],
        data: () => ({
            taskAll: true,
            taskWaiting: false,
            taskWorking: false,
            taskPending: false,
            taskSuccessful: false,

            paymentStatus: null,
            complete: false,
        }),
        computed: {
            filterChat () {
                return this.orders.filter((order) => {
                    if(this.taskAll) {
                        return order
                    } else if(this.taskWaiting) {
                        return order.quotation.workStatus === 'wait for payment'
                    } else if(this.taskWorking) {
                        return order.quotation.workStatus === 'in progress'
                    } else if (this.taskPending) {
                        return order.quotation.workStatus === 'pending'
                    } else if (this.taskSuccessful) {
                        return order.quotation.workStatus === 'successful'
                    }
                })
            }
        },
        methods: {
            taskAllFunc () {
                this.taskAll = true
                this.taskWaiting = false
                this.taskWorking = false
                this.taskSuccessful = false
            },
            taskWaitingFunc () {
                this.taskWaiting = true
                this.taskAll = false
                this.taskWorking = false
                this.taskSuccessful = false
            },
            taskWorkingFunc () {
                this.taskWorking = true
                this.taskAll = false
                this.taskWaiting = false
                this.taskSuccessful = false
            },
            taskPendingFunc () {
                this.taskPending = true
                this.taskAll = false
                this.taskWaiting = false
                this.taskWorking = false
                this.taskSuccessful = false
            },
            taskSuccessfulFunc () {
                this.taskSuccessful = true
                this.taskAll = false
                this.taskWaiting = false
                this.taskWorking = false
                this.taskPending = false
            }
        }
    }
</script>

